
body {
  font-size: 15px;
  line-height: 1.65;
  font-weight: 400;
  color: #666666;
  background-attachment: fixed;
  background-repeat: repeat;
}

.box-layout {
  max-width: 1200px;
  margin: 0 auto;
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.25);
  background-color: #fff;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
  border: 0px solid;
  outline: none;
}

a:hover,
a:active,
a:focus {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  color: #222222;
  margin: 0 0 15px;
}


input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="url"],
input[type="search"],
textarea {
  border: 1px solid #ddd;
  padding: 10px;
}

input[type="submit"],
button[type="submit"] {
  border: none;
  background-color: #065FD4;
  color: #fff;
  padding: 11px 20px;
  -webkit-transition: .25s ease-in;
  transition: .25s ease-in;
}

input[type="submit"]:hover,
button[type="submit"]:hover {
  background-color: #333333;
}

.button {
  background: #065FD4 !important;
  color: #fff !important;
}

.dostart-page-content .wpb_text_column ul {
  margin: 0px 0 0 0;
  padding: 0 !important;
  list-style: none !important;
}

.dostart-page-content .wpb_text_column li {
  position: relative;
  margin-bottom: 15px;
  padding: 0 0 0 28px;
}

.dostart-page-content .wpb_text_column li:before {
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  font-family: fontAwesome;
  content: "\f138";
  font-size: 15px;
  line-height: 23px;
  color: #000033;
  font-weight: bold;
}

blockquote {
  border-left: 5px solid;
}

::-webkit-input-placeholder {
  color: gray;
  -webkit-transition: opacity 250ms ease-in-out;
  transition: opacity 250ms ease-in-out;
}


.section-padding {
  padding: 70px 0;
  background: #fafafa;
  overflow: hidden;
}


/*-------[ Default Responsive ] ------*/

@media( max-width: 1200px) {}

@media( max-width:991px) {}

@media( max-width:767px) {
  body {
    font-size: 14px;
  }
  blockquote {
    font-size: 16px;
    font-style: italic;
  }
  .error-title h1 {
    font-size: 80px;
  }
  .error404 h3 {
    line-height: 1.3;
  }
}

@media( max-width:480px) {}

@media( max-width:320px) {}
